import Bugsnag from "@bugsnag/js"
import BugsnagPluginReact from "@bugsnag/plugin-react"

let _artist = null

export function registerArtist(artist) {
    _artist = artist
}

export function currentArtist() {
    return _artist
}

export function isNullOrUndefined(param) {
    return typeof param === "undefined" || param === null
}

export function isBuilding() {
    return process.env.GATSBY_IS_BUILDING === "true"
}

export function getEnv() {
    let env
    if (!isNullOrUndefined(process.env.GATSBY_ENV)) {
        env = process.env.GATSBY_ENV.trim()
    } else {
        env = process.env.NODE_ENV.trim()
    }
    return env
}

export function isProduction() {
    return getEnv() === "production"
}

Bugsnag.start({
    apiKey: "cb11cfa7b20b9131b55b562f21ac9b9b",
    plugins: [new BugsnagPluginReact()],
    releaseStage: isBuilding() || getEnv(),
    enabledReleaseStages: ["production", "staging", "development"],
})


let hasOwn = {}.hasOwnProperty

export function classNames() {
    let classes = []

    for (let i = 0; i < arguments.length; i++) {
        let arg = arguments[i]
        if (!arg) continue

        let argType = typeof arg

        if (argType === "string" || argType === "number") {
            classes.push(arg)
        } else if (Array.isArray(arg) && arg.length) {
            let inner = classNames.apply(null, arg)
            if (inner) {
                classes.push(inner)
            }
        } else if (argType === "object") {
            for (let key in arg) {
                if (hasOwn.call(arg, key) && arg[key]) {
                    classes.push(key)
                }
            }
        }
    }

    return classes.join(" ")
}


